import React, { useState, Fragment } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { NavLink } from 'react-router-dom'
import clsx from 'clsx';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import Collapse from '@material-ui/core/Collapse';
import MenuItem from '@material-ui/core/MenuItem';
import Menu from '@material-ui/core/Menu';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import PollIcon from '@material-ui/icons/Poll';
import AssignmentTurnedInIcon from '@material-ui/icons/AssignmentTurnedIn';
import Tooltip from '@material-ui/core/Tooltip';
import Grid from '@material-ui/core/Grid';
import ActiveAvatar from '../../components/ActiveAvatar'
import PassiveAvatar from '../../components/PassiveAvatar/'
import DelConfirm from '../DelConfirm'

const useStyles = makeStyles((theme) => ({
  root: {
    maxWidth: '100%',
    margin: '10px 0'
  },
  header: {
    padding: '16px 16px 0 16px'
  },
  content: {
    flex: 1,
    padding: '10px 16px 0 16px'
  },
  media: {
    height: 0,
  },
  expand: {
    transform: 'rotate(0deg)',
    marginLeft: 'auto',
    transition: theme.transitions.create('transform', {
      duration: theme.transitions.duration.shortest,
    }),
  },
  expandOpen: {
    transform: 'rotate(180deg)',
  }
}));

const PollCard = ({ data, handlePollDel }) => {
  const classes = useStyles();
  const [pollId, setPollId] = useState(data.id)
  const [expanded, setExpanded] = useState(false);
  const [anchorEl, setAnchorEl] = useState(false);
  const [delDialogOpen, setDelDialogOpen] = useState(false)
  const handleExpandClick = () => {
    setExpanded(!expanded);
  };

  const isPollMenuOpen = Boolean(anchorEl);

  const Avatar = () => {
    if (data.active) {
      return (
        <ActiveAvatar type={data.type} color={data.color}>
          {data.type}
        </ActiveAvatar>
      )
    } else {
      return (
        <PassiveAvatar type={data.type} color={data.color}>
          {data.type}
        </PassiveAvatar>
      )
    }
  }

  const handlePollMenuOpen = (e) => {
    setAnchorEl(e.currentTarget);
  };

  const handlePollMenuClose = () => {
    setAnchorEl(null);
  }

  const handleDelQuery = () => {
    handlePollMenuClose()
    setDelDialogOpen(true)
  }

  const closeDelDialog = () => {
    setDelDialogOpen(false)
  }

  const delConfirm = () => {
    handlePollDel(pollId)
  }

  const renderPollMenu = (
    <Menu
      anchorEl={anchorEl}
      anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
      id={'poll-menu'}
      keepMounted
      transformOrigin={{ vertical: 'top', horizontal: 'right' }}
      open={isPollMenuOpen}
      onClose={handlePollMenuClose}
    >
      <NavLink to={`/poll-settings/${data.id}/${data.code}`}>
        <MenuItem data-poll={data.id}>
          
        </MenuItem>
      </NavLink>
      <MenuItem onClick={handleDelQuery} data-poll={data.id}></MenuItem>
    </Menu>
  )
  const Results = () => {
    return (
      <Tooltip title="">
        <NavLink to={`/results/${data.id}/${data.code}`}>
          <IconButton>
            <PollIcon />
          </IconButton>
        </NavLink>
      </Tooltip>
    )
  }
  const Drive = () => {
    return (
      <Tooltip title=" ">
        <NavLink to={`/drive/${data.id}/${data.code}`}>
          <IconButton aria-label="share" >
            <AssignmentTurnedInIcon />
          </IconButton>
        </NavLink>
      </Tooltip>
    )
  }
  return (
    <Fragment>
      <DelConfirm open={delDialogOpen} close={closeDelDialog} confirm={delConfirm} />
      <Card className={classes.root}>
        <CardHeader className={classes.header}
          avatar={
            <Avatar />
          }
          action={
            <IconButton aria-label="settings" onClick={handlePollMenuOpen}>
              <MoreVertIcon />
            </IconButton>
          }
          title={data.code + ': ' + data.title}
          subheader={data.startDate + ' - ' + data.endDate}
        />
        <CardContent className={classes.content}>
          <Grid container spacing={1}>
            <Grid item xs={12} sm={6}>
              <Typography variant="button" display="block" gutterBottom>
                 : {data.way}
              </Typography>
            </Grid>
            <Grid item xs={12} sm={6}>
              <Typography variant="button" display="block" gutterBottom>
                /: {data.questionsCount + '/' + data.answersCount}
              </Typography>
            </Grid>
          </Grid>
          <Grid container spacing={1}>
            <Grid item xs={12} sm={6}>
              <Typography variant="overline" display="block" gutterBottom>
                : {data.sample}
              </Typography>
            </Grid>
            <Grid item xs={12} sm={6}>
              <Typography variant="overline" display="block" gutterBottom>
                : {data.complete}
              </Typography>
            </Grid>
          </Grid>
        </CardContent>
        <CardActions disableSpacing>
          <Results />
          <Drive />
          <IconButton
            className={clsx(classes.expand, {
              [classes.expandOpen]: expanded,
            })}
            onClick={handleExpandClick}
            aria-expanded={expanded}
            aria-label="show more"
          >
            <ExpandMoreIcon />
          </IconButton>
        </CardActions>
        <Collapse in={expanded} timeout="auto" unmountOnExit>
          <CardContent>
            <Typography paragraph>:</Typography>
            <Typography paragraph>
                    
          </Typography>
            <Typography paragraph>
                        !!!!!!
          </Typography>
            <IconButton aria-label="share" >
              <AssignmentTurnedInIcon />
            </IconButton>
            <IconButton aria-label="share" >
              <AssignmentTurnedInIcon />
            </IconButton>
          </CardContent>
        </Collapse>
      </Card>
      {renderPollMenu}
    </Fragment>
  );
}

export default PollCard