import React, { Fragment } from 'react'

const ConfigEditor = () => {
  return (
    <Fragment>
      <h3></h3>
      <p>    </p>
      <div id="rulesformitem" class="formitem">
        <label for="rules" id="ruleslabel">Rules:</label>
        <div class="textwrapper"><textarea id="rules" /></div>
      </div>
    </Fragment>
  )
}

export default ConfigEditor